@font-face {
	font-family: "icons";
	src: url('../fonts/icons/icons.eot');
	src: url('../fonts/icons/icons.eot?#iefix') format('eot'),
		// url('../fonts/icons/icons.woff2') format('woff2'),
		url('../fonts/icons/icons.woff') format('woff'),
		url('../fonts/icons/icons.ttf') format('truetype'),
		url('../fonts/icons/icons.svg#icons') format('svg');
	font-weight: normal;
	font-style: normal;
}

.icons:before {
	display: inline-block;
	vertical-align: middle;
	font-family: "icons";
	font-style: normal;
	font-weight: normal;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.icons-lg {
	font-size: 1.3333333333333333em;
	line-height: 0.75em;
	vertical-align: -15%;
}
.icons-2x { font-size: 2em; }
.icons-3x { font-size: 3em; }
.icons-4x { font-size: 4em; }
.icons-5x { font-size: 5em; }
.icons-fw {
	width: 1.2857142857142858em;
	text-align: center;
}

.icons-circleArrow-down:before { content: "\EA01"; }
.icons-circleArrow-left:before { content: "\EA02"; }
.icons-circleArrow-right:before { content: "\EA03"; }
.icons-circleArrow-up:before { content: "\EA04"; }



//  icon 変数
//----------------------------------------------------------------------
$icons-circleArrow-down: "\EA01";
$icons-circleArrow-left: "\EA02";
$icons-circleArrow-right: "\EA03";
$icons-circleArrow-up: "\EA04";

//  font
//----------------------------------------------------------------------
@mixin icons($code) {
	content: $code;
	font-family: "icons";
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}